$(function() {
	$("#button").click(function() {
		var greeting = $("#greeting").val();
		var name = $("#name").val();
		var position = $("#position").val();
		var phone = $("#phone").val();
		var fax = $("#fax").val();
		var mobile = $("#mobile").val();
		var mail = $("#mail").val();
		var line1 = $("#line1").val();
		var line2 = $("#line2").val();
		var line3 = $("#line3").val();
		var line4 = $("#line4").val();

		$(".greeting").text(greeting);
		$(".name").text(name);
		$(".position").text(position);
		$(".phone").text(phone);
		$(".phone").text(phone);
		$(".fax").text(fax);
		$(".mail").text(mail);
		$(".mail").attr("href", "mailto:" + mail);
		$(".line1").text(line1);
		$(".line2").text(line2);
		$(".line3").text(line3);
		$(".line4").text(line4);

		if (mobile.length) {
			$(".mobile").text(mobile);
		} else {
			$(".mobile")
				.parent()
				.parent()
				.remove();
		}

		if ($("#deranger").is(":checked")) {
			// do nothing
		} else {
			$(".deranger").remove();
		}

		if ($("#textderanger").is(":checked")) {
			// do nothing
		} else {
			$(".line1").remove();
			$(".line2").remove();
			$(".line3").remove();
			$(".line4").remove();
		}
	});
});
